.directions {
  display: flex;
  justify-content: center;
  position: relative;
  bottom: 10px;
  font-family: Georgia, "Times New Roman", Times, serif;
}
h1 {
  font-family: Georgia, "Times New Roman", Times, serif;
  display: flex;
  justify-content: center;
}

.score-container {
  display: flex;
  margin: 0 auto;
  justify-content: center;
}
.score {
  background: lightgreen;
  padding: 5px;
  border-radius: 6px;
}

.imagelist {
  display: grid;
  grid-row-gap: 10px;
  height: 100%;
  min-width: 100%;
  grid-template-columns: 2fr 2fr 2fr 1fr;
  grid-template-rows: auto;
  margin: 3px;
}
.imagelist img {
  width: 200px;
  height: 200px;
  box-shadow: 10px 10px 5px grey;
  border: solid;
  border-radius: 10px;
}

.header {
  padding: 50px;
  background: #22aeff;
  min-height: 100%;
  min-width: 92%;
  border-radius: 10px;
}
.footer {
  padding: 50px;
  background: #22aeff;
  border-radius: 10px;
  min-width: 65%;
}
